.list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.list .line {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  height: 40px;
  margin-top: 10px;
}

.list .line td > .output {
  border: none;
  width: auto;
  height: auto;
  margin: auto;
}

.list .line .input-td > .output {
  background-color: #b0c4de;
  border: 2px solid #b0c4de;
  padding-right: 25px;
}

.list .line .output-td > .output {
  border: 2px solid #9999cc;
}

.list .icon {
  width: 40px;
  height: 40px;
}



